import { __ } from '@wordpress/i18n';
import Select from 'react-select';
import { BaseControl } from '@wordpress/components';
import { useEffect, useState } from 'react';


export default function SortFieldSelector( { viewId, isSidebar, onChange } ) {
	const labels = {
		selectSortField: __( 'Select a Sort Field', 'gk-gravityview' ),
		sort: __( 'Sort', 'gk-gravityview' )
	};

    const fields = [
        {
            value: '',
            label: labels.selectSortField
        }
    ];
    
    const [options, setOptions] = useState(fields);
    
    const fetchData = async ( viewId ) => {
        try {
            const response = await fetch( gkGravityViewBlocks.ajax_url, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                body: new URLSearchParams({ 
                    action: 'gv_sortable_fields',
                    nonce: gkGravityViewBlocks.nonce,
                    viewid: viewId 
                })
            });
    
            if (response.status === 200) {
                const text = await response.text();
                const parser = new DOMParser();
                const doc = parser.parseFromString(text, 'text/html');
                const optionElements = doc.querySelectorAll('option');
                const newOptions = Array.from(optionElements).map(option => ({
                    value: option.value,
                    label: option.textContent
                }));
                
                setOptions([fields[0], ...newOptions]);
            } else {
                console.error('Error:', response);
            }
        } catch (error) {
            console.error('Fetch error:', error);
        }
    };
    
    useEffect(() => {
        fetchData(viewId);
    }, [viewId]);
    
    const selectedSortField = views.filter( option => option.value === viewId ) || views[ 0 ];

    return (
        <BaseControl className="sort-field-selector" label={ labels.sort }>
            <Select
                aria-label={ labels.sort }
                placeholder={ labels.selectSortField }
                menuPortalTarget={ document.body }
                styles={ { menuPortal: base => ( { ...base, zIndex: 10 } ) } } // A higher z-index is needed to ensure other editor elements don't overlap the dropdown.
                value={ selectedSortField }
                options={ options }
                onChange={ ( e ) => onChange( e.value ) }
                noOptionsMessage={ () => __( 'No Sorting Fields found', 'gk-gravityview' ) }
            />
        </BaseControl>
    );
}    